.footer{
  bottom: 0px;
  position: absolute;
  width: 100%;
  z-index: -1;
}
.footer .footer-bottom{
  background: black;
  padding-top: 20px;


}


.footer .footer-bottom ul li a {
  line-height: 20px;
  color: #fff;
  position: relative;
  margin-left: 23px;
  display: block;
  text-decoration: none;
  font-family: cursive;
}
.footer-title{
  color: #fff;
  font-size: 1.4rem;
  margin-bottom: 20px;
}

.footer-title a{
  color: #fff;
  font-size: 1.4rem;
  text-decoration: none;
}
.disabledPointer{
  cursor: crosshair;
   pointer-events:none;
}