
body{
  margin: 0;

}
ol,ul {
  list-style: none;
}
blockquote,q{
  quotes: none;
}

blockquote:before, blockquote::after,
q::before,q::after {
  content: "";
  content: none;
}
table{
  border-collapse: collapse;
  border-spacing: 0;
}
.skin_color {color: #484922;}

.container{
max-width: 1200px;
margin: 0 auto;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  background: #f3f3f3;
  color: #666;


}

h1,h2,h3,h4,h5,h6{
  font-family: cursive;
}
h1{
  font-size: 1.6rem;
}

a:hover,
a:focus,
a:active{
  color: #487922;
  text-decoration: none;
}


main, .site-context {
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
}


.calc-res{
  font-size: 1.2rem;
}
.calc-res li {
  margin: 10px 0;
}
.well {
  margin-left: 20px;
}

*{
  text-decoration: none;
}