header{
  background-color: rgb(27, 13, 90);
}

.top_bar{
  padding: 0px;
  font-size: 12px;
  border-bottom: .4px solid #3b621c;
  min-height: 40px;
}

.main-header{
  padding: 20px 0 25px;
}
.site-title{
  color: #fff;
}

.header-nav {
    background: #51ac08;
}
.header-nav li {
    transition: all .5s;
    display: inline-block;
    padding: 15px 25px;
}

.header-nav li:hover{
  background-color: rgb(27, 13, 90);
}

.header-nav li a, .header-nav li a:visited {
    color: #fff;
    text-decoration: none;
    display: block;
}